import React, { Component } from 'react';
class Card extends Component {
    state = {  }
    render() { 
        console.log(this.props)
        return ( 
            <div className="cardContainer">
                <div className="cardCover">
                    <img src={require(`../assets/${this.props.info.image}`)}></img>


                </div>
                <div className="cardDetails">
                    <div className="cardName">{this.props.info.name}</div>
                    <div className="rating">{this.props.info.rating} <i className="fas fa-star yellow"></i></div>
                    <div className="price">{this.props.info.price}</div>
                    <a className="shop" href={this.props.info.link}>Shop</a>

                </div>
            </div>
         );
    }
}
 
export default Card;