class HowItWorks extends Component {
    state = { tab: "pso" }

    
    
    render() { 
        let peopleClass = this.state.tab == "po" ? "changeWork active" : "changeWork"
        let employeClass = this.state.tab == "emp" ? "changeWork active" : "changeWork"
        return ( 
            <div className="startTrialContainer">
                <p className="heading">How it Works</p>
                <div className="hiw-tab-box">

                    <div className="tab-link-container">
                        <h1 className={peopleClass} onClick={()=>this.setState({tab: "po"})}>People Operations</h1>
                        <h1 className={employeClass} onClick={()=>this.setState({tab: "emp"})}>Employees</h1>
                    </div>
                    
                    {this.state.tab == "po" ? <PeopleOperations /> : <Employees />
                    }
                </div>

                
                         
            </div>
         );
    }
}
 
export default HowItWorks;